<template>
    <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" @click="openModal('videoModal')">New Video</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
          
        <ConfirmDeleteButton v-if="props.column.field =='action'" url="course/delete_vedio" title="Do want to delete?" v-bind:data="{'id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
      </template>
    </vue-good-table>
    <vs-modal ref="videoModal" title="Course Video" align-top>
      <div class="">
          <form ref="video_form">
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label>Video Name</label>
                  <input type="text" class="form-control" v-model="form.video_name">
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label>Description</label>
                  <textarea class="form-control" v-model="form.description" rows="5"></textarea>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label>Order</label>
                  <input type="text" class="form-control" v-model="form.order">
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label>Duration</label>
                  <input type="text" class="form-control" v-model="form.duration">
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label>Video URL</label>
                  <input type="file" ref="video_file" class="form-control" >
                </div>
              </div>
            </div>
          </form>
        </div>
        <div slot="footer">
          <button class="btn btn-secondary" type="button" @click="closeModal('videoModal')">Close</button>
          <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
        </div>
    </vs-modal>
    <div ref="loader" style="width: 100%;
    height: 100%;
    background-color: #0c0c0c;
    position: fixed;
    z-index: 4554333;
    top: 0;
    left: 0;
    opacity: .7;text-align: center;
    padding-top: 45vh;" :style="[loader ? {'display':'initial'}: {'display':'none'}]">
    <img src="@/assets/frontend/resources/img/Spinner-1s-200px.gif" style="width:65px;" />
    </div>
  </div>
</template>
<style scoped>
  span.fa{
    margin: 5px;
    cursor: pointer;
  }
  .action-div{
    text-align: right;
  }
</style>
<script>
import { VueGoodTable } from 'vue-good-table'
import { get,post } from '../helpers/api'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import swal from 'sweetalert';
import VsModal from 'vs-modal';
export default {
    name:"CourseLevels",
    components:{ VueGoodTable, ConfirmDeleteButton,VsModal},
     data(){
      return {
        columns: [
          {
            label: 'Name',
            field: 'strVedioName',
          },
          {
            label: 'Description',
            field: 'discription'
          },
          {
            label: 'Duration',
            field: 'duration'
          },
          {
            label: 'Order',
            field: 'intVedioOrder'
          },
          {
            label: 'Status',
            field: 'chrStatus',
          },
          {
            label: 'Created Time',
            field: 'createdTime',
          },
          
          {
            label : 'Action',
            field : 'action'
          }
        ],
        loader:false,
        course_id:"",
        level_id:"",
        rows: [],
        form:{
          video_name:"",
          description:"",
          order:"",
          duration:"",
          video_url:"",
          imae_url:""

        },
        isLoading: false,
        fullPage: true
      }
    },
    created(){
      this.loadData();
      this.course_id = this.$route.params.course_id;
      this.level_id = this.$route.params.level_id;
    },
    methods:{
      loadData () { 
        get('course/get_course').then(res=>{
          this.rows = res.data.arrLevels.filter((e)=>{
              return e.id == this.$route.params.level_id;
          });
          this.rows = this.rows[0].arrVedios
        });
      },

      rowClick(){
        //swal("Success!", "New Referral Created", "success");
      },

      async submitForm(){
          this.loader = true;
          let video_files = this.$refs['video_file'].files;
          if(video_files.length){
              let video_form = new FormData();
              video_form.append('vedio',video_files[0])
              await post('http://65.0.249.156:3001/vedio_upload',video_form).then((resolve)=>{
                  this.form.video_url = resolve.data.filename
                  this.form.imae_url = ""
              })
              .then(()=>{
                post('course/create_vedio',{
                      fkCourseId:this.course_id,
                      arrVedios: [{
                          strVedioName: this.form.video_name,
                          strUrl: this.form.video_url,
                          fkLevelId: this.level_id,
                          discription: this.form.description,
                          imgUrl:this.form.imae_url,
                          intVedioOrder: this.form.order,
                          duration: this.form.duration
                        }]
                      }).then(()=>{
                      this.$refs['videoModal'].close()
                      swal("Success!", "New Video Created", "success");
                      this.form.video_name="",
                      this.form.description="",
                      this.form.order="",
                      this.form.duration="",
                      this.form.video_url="",
                      this.form.imae_url=""
                      this.loadData();
                      this.loader = false;
                  });
              })

          }
        }

    }
}
</script>