<template>
  <div>
    <div class="row">
      <div class="col-md-12">
        <button class="btn btm-sm btn-success pull-right" @click="openModal('roeModal')">New ROE</button>
      </div>
    </div>
    <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
          <ConfirmDeleteButton v-if="props.column.field =='action'" url="common/delete_item" title="Do want to delete?" v-bind:data="{'type':'roe','id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
      </template>
    </vue-good-table>
    <vs-modal ref="roeModal" title="ROE" align-top>
        <div class="">
            <form>
              <div class="row">
                <div class="col-md-12">
                  <div class="form-group">
                    <label>REO</label>
                    <input type="text" class="form-control" v-model="form.reo">
                  </div>
                </div>
              </div>
            </form>
          </div>
          <div slot="footer">
            <button class="btn btn-secondary" type="button" @click="closeModal('roeModal')">Close</button>
            <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
          </div>
    </vs-modal>
      <div class="modal fade" id="roeModal" data-coreui-backdrop="static" data-coreui-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLiveLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="staticBackdropLiveLabel">REO</h5>
              <button class="btn-close" type="button" data-coreui-dismiss="modal" aria-label="Close"></button>
            </div>
            
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import { VueGoodTable } from 'vue-good-table'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import { post } from '../helpers/api'
import swal from 'sweetalert';
import VsModal from 'vs-modal';

export default {
  name:"ROEPage",
  components:{ VueGoodTable,ConfirmDeleteButton,VsModal},
  data() {
    return {
      columns: [
        {
          label: 'ROE',
          field: 'roe',
        },
        {
          label: 'Created On',
          field: 'createdTime',
          //type: 'date',
          // dateInputFormat: 'yyyy-MM-dd HH:mm:ss', 
          // dateOutputFormat: 'MMM do yyyy'
        },
        {
          label: 'Action',
          field : 'action'

        }
      ],
      rows: [],
      form:{
        reo:''
      }
    }
  },
  created(){
    this.loadData();
  },
  methods:{
    loadData(){
      post('common/get_item',{ "type":"roe"}).then(res=>{
        console.log(res)
        this.rows = res.data.arrList;
      })
    },

    async submitForm(){
        await post('common/create_item',{type:'roe',roe:this.form.reo}).then(()=>{
           swal("Success!", "New Reo Created", "success");
           this.form.reo = "";
           this.$refs['roeModal'].close()

        });
    }
  }
}
</script>