<template>
    <div>
        <button class="btn" :class="this.button_class" @click="click" v-bind:data-id=id>{{this.button_text}}</button>
    </div>
</template>
<style scoped>

</style>
<script>
import custom from '../helpers/api'
import swal from 'sweetalert';
export default {
    name:"ConfirmDeleteButton",
    props:{ 
        id:String,
        url: String ,
        title:String,
        button_text:{
            default :'Delete',
            type:String
        },
        button_class:{
            default :'btn-danger',
            type:String
        },
        baseurl:{
            default :'http://65.0.249.156:3000',
            type:String
        },
        subtext:
            {   default:"",
                type:String
            },
        method: 
            {   default:"DELETE",
                type:String
            },
        message: 
            {   default:"Deleted Successfuly",
                type:String
            },
        data:
            {
                type: Object
            },
        callback:Function
    },
    methods:{
         click(){
           swal({
                title: this.title,
                text: this.subtext,
                icon: "warning",
                buttons: true,
            })
            .then(async (willDelete) => {
                if (willDelete) {
                    console.log(this.data);
                    //console.log(JSON.parse(this.data));
                    await custom(this.method,this.url,this.data,{},this.baseurl).then(()=>{
                        swal(this.message, {
                            icon: "success",
                         }).then(()=>{
                             this.callback();
                         });
                    })
                   
                }
            });
        }
    }
}
</script>