<template>
    <div>
    </div>
</template>
<script>
import {post} from '@/helpers/api'
import swal from 'sweetalert';
export default {
    name:"UserReferrals",
    components:{},
    data(){
        return{
            userList:[],
            refUsers:[],
            form:{
              mobile:""
            }
        }
    },
    created(){
       this.loadData();
    },
    methods:{
      loadData(){
        post('referral/get_referral').then(res=>{
          this.userList = res.data.arrList;
        })
      },
      async loadRef(event){
       await post('referral/get_referral',{id:event.target.dataset.id}).then(res=>{
          this.refUsers = res.data.arrList;
          this.$refs['refferalModal'].open()
        })
      },
      async submitForm(){
        await post('referral/create_referral',{strMobile:this.form.mobile}).then(()=>{
            this.$refs['newRefferalModal'].close()
            swal("Success!", "New Referral Created", "success");
            this.form.mobile = "";
            this.loadData();
        });
      }
    }
}
</script>