<script>
import { VueGoodTable } from 'vue-good-table';
// import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import { post } from '../helpers/api'
import swal from 'sweetalert';
export default {
    name:"User",
    components:{ VueGoodTable },
    data(){
      return {
        columns: [
          {
            label: 'Full Name',
            field: 'strFirstName',
          },
          {
            label: 'Mobile',
            field: 'strMobile',
          },
           {
            label: 'Username',
            field: 'strUserName',
          },
          {
            label: 'Email',
            field: 'strEmail',
          },
          {
            label: 'Referral Code',
            field: 'strReferralCode'
          },
          {
            label: 'Created On',
            field: 'createdTime',
          }
        ],
        rows: [],
        packages:[],
        form:{
          referral_code:"",
          username:"",
          password:"",
          email:"",
          mobile:"",
          firstname:"",
          package:"",
        },
        userList:[]
    }
    },
    created(){
      this.loadData();
    },
    methods:{
      loadData(){
        post('user/get_user').then(res=>{
          console.log(res)
          this.rows = res.data.arrList;
        });

        post('common/get_drop_down').then(res=>{
          console.log(res)
          this.packages = res.data.arrList;
        });

      },
      async loadRef(event){
       await post('referral/get_referral',{id:event.target.dataset.id}).then(res=>{
          this.userList = res.data.arrList;
          this.$refs['refferalModal'].open()
        })
      },
      async submitForm(){
        await post('user/create_user',{
            strReferralCode : this.form.referral_code,
            strUserName : this.form.username,
            strPassword : this.form.password,
            strUserType : 'CUSTOMER',
            strEmail : this.form.email,
            strMobile : this.form.mobile,
            fkPackageId : this.form.package.toString(),
            strFirstName : this.form.firstname,
        }).then(()=>{
           swal("Success!", "New User Created", "success");
           this.form.reo = "";
           this.loadData();
        });
      }
    }
}
</script>
