<template>
 <div>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
          <div  v-if="props.column.field =='action' && props.row.strStatus != 'COMPLETED' "> 
            <ConfirmDeleteButton style="display: inline-block;" baseurl="http://65.0.249.156:3002" method="POST" url="wallet/update_retruns" button_class="btn-success btn-sm" button_text="Accept" message="Successfuly Accepted" title="Do want to accept?" v-bind:data="{'id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
            <ConfirmDeleteButton style="display: inline-block; margin-left: 12px;" baseurl="http://65.0.249.156:3002"  method="POST" url="wallet/update_retruns" button_class="btn-danger btn-sm" button_text="Reject" message="Successfuly Rejected" title="Do want to reject?" v-bind:data="{'type':'REJECT','id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
          </div>
      </template>
    </vue-good-table>
   </div>
</template>

<script>

import { VueGoodTable } from 'vue-good-table'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import { post } from '../helpers/api'
// import swal from 'sweetalert';
export default {
  name:"Transation",
  components:{ VueGoodTable,ConfirmDeleteButton},
  data() {
    return {
      columns: [
        {
          label: 'Amount',
          field: this.amountColValue,
        },
        {
          label: 'ROE',
          field: 'roe',
        },
       
        {
          label: 'Description',
          field: 'strText'
        },
        {
          label: 'Doc ID',
          field: 'strDocId'
        },
        {
          label: 'Payment Method',
          field: 'strPayMode'
        },
        {
          label: 'Created On',
          field: 'createdTime',
        },
        {
          label: 'Action',
          field : 'action'

        }
      ],
      rows: [],
    }
  },
  created(){
    this.loadData();
  },
  methods:{
    loadData(){
      post('returns/get_return',{},{},'http://65.0.249.156:3002').then(res=>{
        console.log(res)
        this.rows = res.data.arrList;
      })
    },
    amountColValue(row){
      let amount =  row.intCreditAmount == 0 ? row.intDebitAmount:row.intCreditAmount;
      let baseAmount = row.intBaseCreditAmount == 0 ? row.intBaseDebitAmount:row.intBaseCreditAmount;
      return `${amount} (${baseAmount})`;
    }
  }
}
</script>