<template>
   <div class="bg-light min-vh-100 d-flex flex-row align-items-center">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-lg-7">
            <div class="card-group d-block d-md-flex row">
              <div class="card col-md-12 p-4 mb-0">
                <div class="card-body">
                  <h1>Signup</h1>
                  <p class="text-medium-emphasis">Signup to your account</p>
                  <div class="row">
                      <div class="col-md-6">
                        
                        <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-user"></use>
                            </svg></span>
                          <input class="form-control" type="text" placeholder="Full Name" v-model="form.full_name" tabindex="1">
                        </div>

                        <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-envelope-letter"></use>
                            </svg></span>
                          <input class="form-control" type="text" placeholder="Email" v-model="form.email" tabindex="3">
                        </div>

                        <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-link-broken"></use>
                            </svg></span>
                          <select class="form-control" placeholder="Referral" v-model="form.package" tabindex="5">
                              <option v-for="pack in packages" :key="pack.id" :value="pack.id">
                                {{ pack.strName}}
                              </option>
                          </select>
                        </div>

                         <div class="input-group mb-4">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-lock-locked"></use>
                            </svg></span>
                          <input class="form-control" type="password" placeholder="Password" v-model="form.password" tabindex="7">
                        </div>

                      </div>
                      <div class="col-md-6">

                        <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-mobile"></use>
                            </svg></span>
                          <input class="form-control" type="text" placeholder="Mobile" v-model="form.mobile" tabindex="2">
                        </div>

                        <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-link-broken"></use>
                            </svg></span>
                          <input class="form-control" type="text" placeholder="Referral" v-model="form.referral" tabindex="4">
                        </div>

                         <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-user"></use>
                            </svg></span>
                          <input class="form-control" type="text" placeholder="Username" v-model="form.username" tabindex="6">
                        </div>

                         <div class="input-group mb-3">
                          <span class="input-group-text">
                            <svg class="icon">
                              <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-link-broken"></use>
                            </svg></span>
                          <input class="form-control" type="password" placeholder="Confirm Password" v-model="form.confirm_password" tabindex="8">
                        </div>
                        

                      </div>
                  </div>
                  
                 
                  <div class="row">
                    <div class="col-6">
                     <button class="btn btn-primary px-4" type="button" @click="signup">Signup</button>
                    </div>
                    <div class="col-6 text-end">
                     <router-link to="login"> <button class="btn btn-link px-0" type="button">Login</button></router-link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import {post} from '../helpers/api'
import swal from 'sweetalert';
export default {
    name:"Signup",
    data(){
      return {
        packages:[],
        form : {
          full_name:"",
          email:"",
          mobile:"",
          referral:"",
          package:"2",
          username:"",
          password:"",
          confirm_password:""
        }
      }
    },
    created(){
        post('common/get_drop_down',{type:"package"}).then(res=>{
          this.packages = res.data.arrList;
        });
        // swal("Success!", "New Referral Created", "success");
    },
    methods:{


      async signup(){
        if(this.form.full_name == ""){
          swal("Name Required", "", "error");
          return "";
        }

        if(this.form.email == ""){
          swal("Email Required", "", "error");
          return "";
        }

        if(this.form.mobile == ""){
          swal("Mobile Required", "", "error");
          return "";
        }

        if(this.form.referral == ""){
          swal("Referral Required", "", "error");
          return "";
        }

        if(this.form.username == ""){
          swal("Username Required", "", "error");
          return "";
        }

      
        if(this.form.password !== this.form.confirm_password){
          swal("Password not matches", "", "error");
          return "";
        }

        await post('user/create_user',{
          'strReferralCode':this.form.referral,
          'strUserName':this.form.username,
          'strPassword':this.form.password,
          'strUserType':"CUSTOMER",
          'strEmail':this.form.email,
          'strMobile':this.form.mobile,
          'fkPackageId':this.form.package,
          'strFirstName':this.form.full_name,
          },{Authorization:""}).then((res)=>{
            console.log('ss',res)
            swal('Successfuly Signup',"","success").then(()=>{
              location.href = '/'
            })
            
        })
        .catch(()=>{
          //console.log('ee',res)
        })
      }
    }
}
</script>