import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import dotenv from 'dotenv'

Vue.mixin({
  methods: {
    openModal(ref){
      this.$refs[ref].open();
      //alert(ref);
    },
    closeModal(ref){
      this.$refs[ref].open();
    }
  },
})

Vue.config.productionTip = false
dotenv.config();


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
