<template>
    <div class="news">
            <div class="news-sec">

              <div class="newscard" v-for="news in newsList" :key="news.id">
                <div class="newscard-top">
                  <p class="newscard-top-usertext">{{news.jsnDetails.strHead}}</p>
                  <p class="newscard-top-time">{{news.createdTime}}</p>
                </div>
                <div style="display:flex">
                <p class="newscard-content">{{news.jsnDetails.strDiscription}}</p>
               <img
                  style="float: right; margin: 12px;"
                  :src="news.jsnDetails.strUrl"
                  alt=""
                  class="newscard-content-img"
                />
                </div>
              </div>
             
            </div>
            <div class="news-side"></div>
          </div>
</template>
<script>
import {post} from '@/helpers/api'
export default {
    name:"UserNews",
    data(){
        return{
            newsList:[]
        }
    },
    created(){
        post('news/get_news').then(res=>{
          this.newsList = res.data.arrList;
        })
    }
}
</script>