import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../layout/Login'
import Signup from '../layout/Signup'
import LayoutDashoard from '../layout/Dashboard'
import Dashboard from '../views/Dashboard'
import Course from '../views/Course'
import User from '../views/User'
import News from '../views/News'
import Referral from '../views/Referral'
import ROE from '../views/ROE'
import Transaction from '../views/Transaction'
import CourseQuestions from '../views/CourseQuestions'
import CourseLevels from '../views/CourseLevels'
import CourseVideos from '../views/CourseVideos'
import Account from '../views/Account'
import Trade from '../views/Trade'

import Layout from '../layout/frontend/Layout'
import UserDashboard from '../views/frontend/Dashboard'
import UserCourse from '../views/frontend/Course'
import UserCourseDetails from '../views/frontend/CourseDetails'
import UserNews from '../views/frontend/News'
import UserReferrals from '../views/frontend/Referral'
// import UserQuestion from '../views/frontend/Questions'

Vue.use(VueRouter)

const Roles={'user':'CUSTOMER','admin':'ADMIN'};
const RoleIndexPage={'CUSTOMER':'/dashboard','ADMIN':'/admin/dashboard'}
const routes = [
  {
    path: '/',
    name: 'login',
    component: Login,
    meta:{authRequired:false}
  },
  {
    path:'/signup',
    name: 'signup',
    component: Signup,
    meta:{authRequired:false}
  },
  {
    path: '/',
    name: 'user_layout',
    component: Layout,
    children: [
      {
        path:'/dashboard',
        name: 'user_dashboard',
        component: UserDashboard,
        meta:{authRequired:true,'authRole':Roles.user}
      },
      {
        path: '/courses',
        name: 'user_course',
        component: UserCourse,
        meta:{authRequired:true,'authRole':Roles.user}
      },
      {
        path: '/news',
        name: 'user_news',
        component: UserNews,
        meta:{authRequired:true,'authRole':Roles.user}
      },
      {
        path: '/referrals',
        name: 'user_referrals',
        component: UserReferrals,
        meta:{authRequired:true,'authRole':Roles.user}
      },
      // {
      //   path: '/quiz/:course_id/:level_id',
      //   name:'user_quiz',
      //   component :UserQuestion,
      //   meta:{authRequired:true,'authRole':Roles.user}
      // }
      
    ]
  },
  {
    path: '/course/:id/details',
    name: 'user_course_details',
    component: UserCourseDetails,
    meta:{authRequired:true,'authRole':Roles.user}
  },
  {
    path: '/admin',
    component:LayoutDashoard,
    children :[
      {
        path:'dashboard',
        name:'admin_dashboard',
        component : Dashboard,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'courses',
        name:"admin_courses",
        component : Course,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'course/:id/levels',
        name: "admin_course_levels",
        component : CourseLevels,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'course/:course_id/level/:level_id/videos',
        name: "admin_course_level_videos",
        component : CourseVideos,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'course/:course_id/level/:level_id/questions',
        name: "admin_course_questions",
        component : CourseQuestions,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'users',
        name:"admin_users",
        component : User,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'news',
        name:"admin_news",
        component : News,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'referrals',
        name:"admin_referrals",
        component : Referral,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'roe',
        name:"admin_roe",
        component : ROE,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'account',
        name:"admin_account",
        component : Account,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'trade',
        name:"admin_trade",
        component : Trade,
        meta:{authRequired:true,'authRole':Roles.admin}
      },
      {
        path:'transactions',
        name:"admin_transactions",
        component : Transaction,
        meta:{authRequired:true,'authRole':Roles.admin}
      }
      
   ]
    // meta:{ layout: LayoutDashoard }
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => aimport(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]


const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  
  const user_type = localStorage.getItem('user_type');
  if(to.meta.authRequired){
    if(user_type && localStorage.getItem('user_token')){
      if(user_type  == to.meta.authRole){
        next();
      }else{
      window.location.href = RoleIndexPage[user_type]
      }
    }
    else
      window.location.href = '/';
  }
  else
    next();
});

export default router
