<template>
  <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" @click="openModal('newRefferalModal')">New Referrals</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
       <template slot="table-row" slot-scope="props">
          <button class="btn btn-primary" v-if="props.column.field =='action'" :data-id="props.row.id" @click="loadRef">Reffered Users</button>
      </template>
    </vue-good-table>
<vs-modal ref="newRefferalModal" title="Refferal" align-top>
   <div class="">
        <form>
          <div class="row">
            <div class="col-md-12">
              <div class="form-group">
                <label>Mobile</label>
                <input type="text" class="form-control" v-model="form.mobile">
              </div>
            </div>
          </div>
        </form>
    </div>
      <div slot="footer">
        <button class="btn btn-secondary" type="button" data-coreui-dismiss="modal">Close</button>
        <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
      </div>
</vs-modal>
     <vs-modal ref="refferalModal" title="Refferal" align-top >
          <div class="">
                <div class="row">
                  <div class="col-md-12">
                    <table style="width:100%" border="1">
                      <tr class="" v-for="user in refUserList" :key="user.id" style="border: 1px solid;">
                        <td style="border: 1px solid;padding: 6px;">{{user.strUser}}</td>
                        <td style="border: 1px solid;padding: 6px;">{{user.strMobile}}</td>
                        <td style="border: 1px solid;padding: 6px;">{{user.intRewardAmount}}</td>
                        <td style="border: 1px solid;padding: 6px;">{{user.strReferralCode}}</td>
                        <td style="border: 1px solid;padding: 6px;">{{user.createdTime}}</td>
                      </tr>
                    </table>
                  </div>
                </div>
          </div>
      </vs-modal>
  </div>
</template>
<script>
import { VueGoodTable } from 'vue-good-table'
// import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import { post } from '../helpers/api'
import swal from 'sweetalert';
import VsModal from 'vs-modal';

export default {
  name:"Referrals",components:{ VueGoodTable,VsModal },
    data(){
      return {
        columns: [
          {
            label: 'Mobile',
            field: 'strMobile',
          },
          {
            label: 'Referral Code',
            field: 'strReferralCode'
          },
          {
            label: 'Referral Status',
            field: 'strReferralStatus',
          },
          {
            label: 'Reward Amount',
            field: 'intRewardAmount',
          },
          {
            label: 'ROE',
            field: 'roe',
          },
          {
            label: 'Referred By',
            field: 'strReferredBy',
          },
          {
            label: 'User',
            field: 'strUser',
          },
          {
            label:'Action',
            field:'action'
          }
        ],
        rows: [],
        refUserList:[],
        form:{
          mobile:''
        }
      }
    },
    created(){
      this.loadData();
    },
    methods:{
      async loadRef(event){
       await post('referral/get_referral',{id:event.target.dataset.id}).then(res=>{
          this.refUserList = res.data.arrList;
          this.$refs['refferalModal'].open()
        })
      },
      
      loadData () { 
        post('/referral/get_referral').then(res=>{
          this.rows = res.data.arrList;
        })
      },
      async submitForm(){
        await post('referral/create_referral',{strMobile:this.form.mobile}).then(()=>{
            this.$refs['refferalModal'].close()
            swal("Success!", "New Referral Created", "success");
            this.form.mobile = "";
            this.loadData();
        });
      }

    }
}
</script>
