<template>
   <div class="bg-light min-vh-100 d-flex flex-row align-items-center">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-lg-4">
            <div class="card-group d-block d-md-flex row">
              <div class="card col-md-4 p-4 mb-0">
                <div class="card-body">
                  <h1>Login</h1>
                  <p class="text-medium-emphasis">Sign In to your account</p>
                  <div class="input-group mb-3"><span class="input-group-text">
                      <svg class="icon">
                        <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-user"></use>
                      </svg></span>
                    <input class="form-control" type="text" placeholder="Username" v-model="form.username">
                  </div>
                  <div class="input-group mb-4"><span class="input-group-text">
                      <svg class="icon">
                        <use xlink:href="@/assets/vendors/@coreui/icons/svg/free.svg#cil-lock-locked"></use>
                      </svg></span>
                    <input class="form-control" type="password" placeholder="Password" v-model="form.password">
                  </div>
                  <div class="row">
                    <div class="col-6">
                     <button class="btn btn-primary px-4" type="button" @click="login">Login</button>
                    </div>
                    <div class="col-6 text-end">
                      <router-link to="signup"> <button class="btn btn-link px-0" type="button">Signup</button></router-link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import {post} from '../helpers/api'
export default {
    name:"Login",
    data(){
      return {
        user_type:"",
        token:"",
        form : {
          username:"",
          password:""
        }
      }
    },
    methods:{
      async login(){
        await post('user/login_user',{'strLoginId':this.form.username,'strPassword':this.form.password},{Authorization:""}).then(res=>{
            console.log(window.location);
            if(res.status == 200){
              localStorage.setItem('user_type',res.data.strUserType);
              localStorage.setItem('user_token',res.data.strToken);
              localStorage.setItem('user_id',res.data.intUserId);
              if(res.strUserType == 'ADMIN')
                window.location.href = '/admin/dashboard'
              else
                window.location.href = '/dashboard'
            }
            else{
              console.log('d')
            }
            
        })
      }
    }
}
</script>