<template>
    <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" @click="openModal('questionModal')">New Question</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
          <!-- <ConfirmDeleteButton v-if="props.column.field =='action'" url="common/delete_item" title="Do want to delete?" v-bind:data="{'type':'roe','id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton> -->
        <div class="action-div" v-if="props.column.field =='action'">
         <ConfirmDeleteButton class="del-button" v-if="props.column.field =='action'" url="common/delete_item" title="Do want to delete?" v-bind:data="{'id':props.row.id,'type':'question' }" v-bind:callback="loadData"></ConfirmDeleteButton>
        </div>
      </template>
    </vue-good-table>
       <vs-modal ref="questionModal" title="Course Question" align-top>
      <div class="">
          <form ref="video_form">
            <div class="row">
              <div class="col-md-12">
                <div class="form-group">
                  <label>Question</label>
                  <input type="text" class="form-control" v-model="form.strQuestion">
                </div>
              </div>
            </div>
           
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label>Option A</label>
                  <input type="text" class="form-control" v-model="form.A">
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label>Option B</label>
                  <input type="text" class="form-control" v-model="form.B">
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label>Option C</label>
                  <input type="text" class="form-control" v-model="form.C">
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label>Option D</label>
                  <input type="text" class="form-control" v-model="form.D">
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label>Answer</label>
                  <select class="form-control" v-model="form.answer">
                    <option value="A">Option A</option>
                    <option value="B">Option B</option>
                    <option value="C">Option C</option>
                    <option value="D">Option D</option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label>Order</label>
                  <input type="text" class="form-control" v-model="form.questenOrder">
                </div>
              </div>
            </div>
          </form>
        </div>
        <div slot="footer">
          <button class="btn btn-secondary" type="button" @click="closeModal('videoModal')">Close</button>
          <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
        </div>
    </vs-modal>
  </div>
</template>
<style scoped>
</style>
<script>
import { post } from '../helpers/api'
import { VueGoodTable } from 'vue-good-table'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import VsModal from 'vs-modal';
import swal from 'sweetalert';
export default {
    name:"CourseQuestions",
    components:{VueGoodTable,ConfirmDeleteButton,VsModal},
    data(){
      return {
        course_id:"",
        level_id:"",
        columns: [
          {
            label: 'Question',
            field: 'jsnQuesten.strQuesten',
          },
          {
            label: 'Option 1',
            field: 'jsnQuesten.A'
          },
         {
            label: 'Option 2',
            field: 'jsnQuesten.B'
          },
          {
            label: 'Option 3',
            field: 'jsnQuesten.C'
          },
          {
            label: 'Option 4',
            field: 'jsnQuesten.D'
          },
          {
            label : 'Action',
            field : 'action'
          }
        ],
        rows:[],
        form:{
          strQuestion:"",
          A:"",
          B:"",
          C:"",
          D:"",
          answer:"",
          questenOrder:""
        }
      }
    },
    created(){
      this.course_id = this.$route.params.course_id;
      this.level_id = this.$route.params.level_id;
      this.loadData();
    },
    methods:{
      loadData () { 
        post('course/get_questens',{fkLevelId:this.level_id,fkCourseId:this.course_id}).then(res=>{
          console.log(res);
          this.rows = res.data.arrList;
        })
      },
      async submitForm(){
        await post('course/create_questens',{
          fkLevelId:this.level_id,
          fkCourseId:this.course_id,
          arrQuestens:[
            {
              strQuesten:this.form.strQuestion,
              A:this.form.A,
              B:this.form.B,
              C:this.form.C,
              D:this.form.D,
              strAnswer:this.form.answer,
              intQuestenOrder:this.form.questenOrder
            }
          ]

        }).then(()=>{
            this.$refs['questionModal'].close();
            swal("Success!", "New Question Created", "success");
            //this.form.mobile = "";
            this.loadData();
        });
       }

    }
}
</script>