<template>
  <div>
      <div class="row">
        <div class="col-md-12">
          <button class="btn btm-sm btn-success pull-right" @click="openModal('newsModal')">New News</button>
        </div>
      </div>
      <br>
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :pagination-options="{
        enabled: true,
        records: 'pages',
      }">
      <template slot="table-row" slot-scope="props">
        <div class="action-div" v-if="props.column.field =='action'">
          <ConfirmDeleteButton class="del-button" v-if="props.column.field =='action'" url="news/delete_news" title="Do want to delete?" v-bind:data="{'id':props.row.id }" v-bind:callback="loadData"></ConfirmDeleteButton>
        </div>
      </template>
    </vue-good-table>
<vs-modal ref="newsModal" title="News" align-top>
   <div class="modal-body">
      <form>
        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>Title</label>
              <input type="text" class="form-control" v-model="form.title">
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>Image</label>
              <input type="file" ref="newsImage" class="form-control" @change="renderImage">
            </div>
          </div>
        </div>
          <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>Description</label>
              <textarea class="form-control" v-model="form.description" rows="10"></textarea>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div slot="footer">
      <button class="btn btn-secondary" type="button" @click="closeModal('newsModal')">Close</button>
      <button class="btn btn-primary" type="button" @click="submitForm">Save</button>
    </div>
  </vs-modal>
     
  </div>
</template>

<script>
import { VueGoodTable } from 'vue-good-table'
import { post } from '../helpers/api'
import ConfirmDeleteButton from '../components/ConfirmDeleteButton.vue'
import swal from 'sweetalert';
import VsModal from 'vs-modal';

export default {
  name:"News",
  components : { VueGoodTable,ConfirmDeleteButton,VsModal },
  data(){
      return {
        columns: [
          {
            label: 'Title',
            field: 'jsnDetails.strHead',
          },
          {
            label: 'Type',
            field: 'chrType'
          },
          {
            label : 'Action',
            field : 'action'
          }
        ],
        rows: [],
        form:{
          title:"",
          description:"",
          meadia_type:"IMAGE",
          url:""
        }
      }
    },
  created(){
    this.loadData();
  },
  methods:{
    loadData () { 
        post('news/get_news').then(res=>{
          this.rows = res.data.arrList;
        })
      },
      renderImage(){

      },
      async submitForm(){
        let newsImage = this.$refs.newsImage;
        if(newsImage.files.length){
          let imageForm = new FormData();
          console.log(newsImage.files[0]);
          imageForm.append('images',newsImage.files[0])
          await post('file/files_upload',imageForm).then(res=>{
            console.log(res)
            this.form.url = res.data.arrUrls[0]?.url
          }).then(()=>{
            post('news/create_news',{jsnDetails:{strHead:this.form.title,strDiscription:this.form.description,strUrl:this.form.url,strMeadiaType:this.form.meadia_type}}).then(()=>{
              this.$refs['newsModal'].close()
              swal("Success!", "New News Created", "success");
              this.form.title = this.form.description = this.form.url = "";
              this.loadData();
            });
          })
        }

        
       }
  }
}
</script>
