<template>
  <div>
    <div class="player-container">
      <div class="player"  style="width: 69.9vw;padding-top: 5%;">
        <video ref="videoControl" width="650" controls muted="muted" autoplay style="width: 100%;margin-top: 9vh;">
          <source ref="videoplayer" :src="videourl" type="video/mp4" />
        </video>
      </div>
      <div class="player-nav" style="width: 30.9vw;">
        <div class="player-nav-top">
          <p class="player-heading">{{course.title}}</p>
          <p class="player-author">By {{course.author}}</p>
          <div class="player-options">
            <p ref="courseLevel" class="player-options-item" v-bind:class="{'player-options-item-active':enableCourse}"
              @click="openLevel">
              Table Of Contents
            </p>
            <p ref="courseNote" class="player-options-item" v-bind:class="{'player-options-item-active':enableNote}"
              @click="openNote">Notes</p>
          </div>
        </div>
        <div ref="level_content" class="player-steps" :class="{'player-steps-active':enableCourse}">
          <div v-for="level in courseLevelList" :key="level.id">
            <div class="player-steps-head">
              <div class="player-steps-icn">
                <img src="@/assets/frontend/resources/icons/course/unlock.svg" alt="" class="player-steps-icn-lock">
              </div>
              <div class="player-steps-text">
                <p class="player-steps-head-texthead">{{level.strName}}</p>
                <p class="player-steps-head-texttime">0h 5m 48s</p>
              </div>
            </div>

            <div class="player-steps-subcategory" v-for="sub_level in level.arrVedios" :key="sub_level.id">
              <div class="player-steps-icn player-steps-icn-play" v-if="sub_level.type === 'V'">
                <img src="@/assets/frontend/resources/icons/course/play-active.svg" alt="" class="player-steps-icn-play"
                  @click="playVideo($event)" v-bind:data-fid="sub_level.fkLevelId"
                  v-bind:data-cid="sub_level.fkCourseId" v-bind:data-videourl="sub_level.strUrl">
              </div>
              <div class="player-steps-text-sub" v-if="sub_level.type === 'V'" @click="playVideo($event)"
                v-bind:data-fid="sub_level.fkLevelId" v-bind:data-cid="sub_level.fkCourseId"
                v-bind:data-videourl="sub_level.strUrl">
                <p class="
                      player-steps-head-texthead player-steps-head-texthead-sub
                    " @click="playVideo($event)" v-bind:data-fid="sub_level.fkLevelId"
                  v-bind:data-cid="sub_level.fkCourseId" v-bind:data-videourl="sub_level.strUrl">
                  {{sub_level.strVedioName}}
                </p>
                <p class="
                      player-steps-head-texttime player-steps-head-texttime-white
                    ">
                  0h 6m 38s
                </p>
              </div>

              <router-link
                :to="{ name:'user_quiz',params: {'course_id':sub_level.fkCourseId,'level_id':sub_level.fkLevelId} }"
                style="margin-left: 4px;">
                <div class="player-steps-icn player-steps-icn-play" v-if="sub_level.type === 'Q'">
                  <img src="@/assets/frontend/resources/icons/course/download.svg" alt="" class="player-steps-icn-play"
                    @click="playVideo($event)" v-bind:data-videourl="sub_level.strUrl">
                </div>
                <div class="player-steps-text-sub" v-if="sub_level.type === 'Q'">
                  <p class="
                        player-steps-head-texthead player-steps-head-texthead-sub">
                    {{sub_level.strVedioName}}
                  </p>
                </div>
              </router-link>
            </div>
          </div>
        </div>
        <div ref="note_content" class="player-steps" :class="{'player-steps-active':enableNote}">
          <!-- <div v-for="note in noteList" :key="note.id">
              <div class="chat__message" v-for="message in note.arrVedios" :key="message.fkCourseId">
                <div>{{message.strVedioName}}</div>
            </div>
            </div>
            <div>
              <input type="text" class="chat-comment-box" v-model="comment" style="height:38px;">
              <button @click="submitComment" class="chat-comment-button" style="height:38px;">Comment</button>
            </div> -->
          <div class="box box-primary direct-chat direct-chat-primary">

            <!-- /.box-header -->
            <div class="box-body">
              <div class="direct-chat-messages">
                <div v-for="note in noteList" :key="note.id">
                <div class="direct-chat-msg" v-for="message in note.arrVedios" :key="message.fkCourseId">
                  <div class="direct-chat-info clearfix">
                    <div class="direct-chat-text">{{message.strVedioName}}</div>
                  </div>
                </div>
                </div>
              </div>
            </div>
            <div class="box-footer">
                <div class="input-group">
                  <input type="text" @keyup.enter="submitComment" placeholder="Type Message ..."  v-model="comment" class="form-control">
                  <span class="input-group-btn">
                    <button type="submit" @click="submitComment" class="btn btn-primary btn-flat" style="background-color: #899cca;">Send</button>
                  </span>
                </div>
            </div>
            <!-- /.box-footer-->
          </div>
          <!--/.direct-chat -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {
    get,
    post
  } from '../../helpers/api'
  export default {
    name: "UserCourseDetails",
    data() {
      return {
        videourl: "",
        course: {
          id: "",
          title: "",
          author: "",
          price: "",
          currency_code: "",
          image_url: "",
          rating: "",
          level: "",
          duration: "",
          last_update: "",
          levels: []
        },
        comment: "",
        courseLevelList: [],
        noteList: [],
        currentCourse: null,
        currentLevel: null,
        enableCourse: true,
        enableNote: false
      }
    },
    created() {
      this.videourl = "";
      get('course/get_course').then(res => {
        this.course.id = res.data.id;
        this.course.title = res.data.strCourseName;
        this.course.author = res.data.strMentorName;
        this.course.price = res.data.intCoursePrice;
        this.course.currency_code = res.data.strCurrencyCode;
        this.course.image_url = res.data.imgUrl;
        this.course.rating = res.data.jsnDetails.Rating;
        this.course.level = res.data.jsnDetails.Level;
        this.course.duration = res.data.jsnDetails.Duration;
        this.course.last_update = res.data.jsnDetails.Updated;
        this.course.levels = res.data.arrLevels;
        this.videourl = "http://65.0.249.156:3001/video?videoPath=" + res.data.arrLevels[0]?.arrVedios[0]?.strUrl;
        this.$refs['videoControl'].load();
        //this.$refs['videoControl'].play();
        this.courseLevelList = this.course.levels.map(item => {
          let copy = Object.assign({}, item);
          let v = copy.arrVedios.filter(obj => {
            this.currentLevel = this.currentLevel ?? obj.fkLevelId;
            this.currentCourse = this.currentCourse ?? obj.fkCourseId;
            return obj.type == 'V' || obj.type == 'Q';
          });
          copy.arrVedios = v;
          return copy;
        });
        //console.log(this.course.levels,this.courseLevelList);
      });
    },
    methods: {
      playVideo(event) {
        this.videourl = "http://65.0.249.156:3001/video?videoPath=" + event.target.dataset.videourl
        this.$refs['videoControl'].load();
        this.$refs['videoControl'].play();
        this.currentCourse = event.target.dataset.cid;
        this.currentLevel = event.target.dataset.fid;

      },
      openNote() {
        this.enableCourse = false;
        this.enableNote = true;
        this.noteList = this.course.levels.map(item => {
          let copy = Object.assign({}, item);
          let v = copy.arrVedios.filter(obj => {
            return obj.type == 'C' && obj.fkCourseId == this.currentCourse && obj.fkLevelId == this
            .currentLevel;
          });
          copy.arrVedios = v;
          return copy
        });
      },
      openLevel() {
        this.enableCourse = true;
        this.enableNote = false;
      },
      async submitComment() {
        await post('news/create_news', {
          APItype: 'COMMENT',
          fkLevelId: this.currentLevel,
          jsnDetails: {
            strText: this.comment
          }
        }).then(() => {
          this.noteList.push({
            "arrVedios": [{
              fkCourseId: this.currentCourse,
              fkLevelId: this.currentLevel,
              strVedioName: this.comment,
              type: "C"
            }]
          })
          this.comment = "";
        })
      }
    }
  }
</script>